import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import avatar from './images/avatar';
import { converToBase64 } from '../base64/Base64';
import './css/editProfileStyle.css';


const EditProfile = () => {
    const navigate = useNavigate();
    const [pass, setPass] = useState();
    const [userData, setUserData] = useState();
    const [loader, setLoader] = useState(false);
    const [error, setError] = useState(false);
    const [errMsg, setErrorMsg] = useState(false);

    const AvatarClick = () => {
        const ProfilePic = document.querySelector('#profileImage');
        ProfilePic.click();
    }


    let name, value;
    //let userInfo = { name: '', email: '', phone: '' };
    const handlePassChange = (event) => {
        setError(false);
        value = event.target.value;
        setPass(value);
    }
    const handleChange = (event) => {
        name = event.target.name;
        value = event.target.value;
        setUserData({ ...userData, [name]: value });
    }

    const handleProfilePic = async (event) => {
        name = event.target.name;
        const file = event.target.files[0];
        console.log(file.size)
        if (file.size > 500000) {
            alert('Image should be less than or equal to 500KB');
        }
        else {
            const base64 = await converToBase64(file);
            setUserData({ ...userData, [name]: base64 });
        }
    }

    const sendPass = async (event) => {
        setError(false);
        setLoader(true);
        event.preventDefault();
        const res = await fetch('/verify', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ pass }),
            credentials: 'include'
        });
        const data = await res.json();
        setLoader(false);
        if (res.status === 200) {
            setUserData(data);
        }
        if (res.status === 500) {
            setError(true);
        }
    }


    const sendData = async (event) => {
        setLoader(true);
        event.preventDefault();
        const { _id, name, email, phone, profileImage } = userData;
        const res = await fetch('/editprofile', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ _id, name, email, phone, profileImage })
        });
        const data = await res.json();
        setLoader(false);
        if (res.status === 201) {
            navigate('/');
        }
        if (res.status === 501) {
            setError(true);
            setErrorMsg(data.message);
        }
    }



    return (
        <div id="profile">
            {userData ?
                <div id='editProfile'>
                    <div className='edit form-input'>
                        <span className="input-group">
                            <i className="fa-solid fa-user"></i>
                        </span>
                        <input type='text' className='editInfo' placeholder='Name'
                            name="name"
                            value={userData.name ? userData.name : ''}
                            onChange={handleChange}
                        />
                    </div>

                    <div className='edit form-input'>
                        <span className="input-group">
                            <i className="fa-solid fa-envelope" />
                        </span>
                        <input type='email' className='editInfo' placeholder='example@email.com'
                            name="email"
                            value={userData.email ? userData.email : ''}
                            onChange={handleChange}
                        />
                    </div>

                    <div className='edit form-input'>
                        <span className="input-group">
                            <i className="fa-solid fa-phone"></i>
                        </span>
                        <input type='number' className='editInfo' placeholder='97XX89XX96'
                            name="phone"
                            value={userData.phone ? userData.phone : ''}
                            onChange={handleChange}
                        />
                    </div>
                    <img src={userData.profileImage || avatar}
                        height='130px'
                        width='130px' alt='Upload'
                        id='profilePhoto'
                        onClick={AvatarClick}></img>
                    <input
                        type='file'
                        name='profileImage'
                        id='profileImage'
                        accept='.jpeg, .jpg, .png'
                        onChange={handleProfilePic}
                    />
                    {error&&<div id="errMsg">{errMsg}</div>}
                    {loader ?
                        <div className='loading'>
                            <div className="loader" style={{ width: '40px', height: '40px' }}></div>
                        </div>
                        :
                        <button
                            type='submit'
                            className='btn btn-primary'
                            id='donebtn'
                            onClick={sendData}
                        >Done</button>
                    }
                </div>

                :

                <div id="verify">
                    <p>Enter password to confirm</p>
                    <input
                        type='password'
                        placeholder='Password'
                        onChange={handlePassChange}
                    ></input>
                    {error && <div id='wrongPass'>Wrong Password</div>}
                    {loader ?
                        <div className='loading'>
                            <div className="loader" style={{ width: '40px', height: '40px' }}></div>
                        </div>
                        :
                        <button
                            type='submit'
                            className='btn btn-primary'
                            id='verifybtn'
                            onClick={sendPass}
                        >Verify</button>
                    }
                </div>
            }
        </div>
    )
}

export default EditProfile;