import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

const ForgotPass = () => {
    const [email, setEmail] = useState();
    const [reply, setReply] = useState();
    const [loader, setLoader] = useState(false);
    const [sent, setSent] = useState(false);

    const sendEmail = async () => {
        setReply(null);
        if (!email) {
            setReply("Please enter email");
            return;
        }
        setLoader(true);
        const options = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                email
            })
        }
        const res = await fetch('/forgot-password', options);
        const result = await res.json();
        setReply(result.message);
        setLoader(false);

        if (res.status === 200){
            setSent(true);
        }
    }


    return (
        <div id="forgotPass">
            {sent ?
                <>
                    <div id='middle'>
                        <svg xmlns="http://www.w3.org/2000/svg" id='svg-success'>
                            <symbol id="check-circle-fill" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"></path>
                            </symbol>
                        </svg>

                        <div className="alert alert-success d-flex align-items-center" role="alert">
                            <svg className="bi flex-shrink-0 me-2" role="img" aria-label="Success:"><use xlinkHref="#check-circle-fill"></use></svg>
                            <div>
                                Mail sent
                            </div>
                        </div>
                    </div>
                    <div className='register'>Reset mail has been sent<br></br>Didn't recieved?&nbsp;
                        <NavLink to='/forgot-password' onClick={()=>{setSent(false); setReply('')}}>Send again</NavLink>
                    </div>
                </>
                :
                <div id="verify" className=''>
                    <p>Enter email to generate reset link</p>
                    <input
                        style={{width:'240px'}}
                        value={email?email:''}
                        type='email'
                        placeholder='Enter your email'
                        onChange={e => { setEmail(e.target.value); setReply(null) }}
                    ></input>
                    {loader ?
                        <div className='loading'>
                            <div className="loader" style={{width:'35px', height:'35px'}}></div>
                        </div>
                        :
                        <button
                            type='submit'
                            className='btn btn-primary'
                            id='verifybtn'
                            onClick={sendEmail}
                        >Send</button>
                    }
                    {reply && <p style={{ color: 'tomato' }}>{reply}</p>}
                </div>
            }
        </div>
    )
}

export default ForgotPass;
